.sidebarConatiner{
    width:27%;
    
}
.cardHeader{
    text-transform: uppercase;
    font-size: 15px;
    text-align: center;
    box-sizing: border-box;
    letter-spacing: 2px;
    color:#333;
}
.profileImageConatiner{
    width:100%;
    box-sizing: border-box;
}
.profileImageConatiner img{
    max-width:100%;
    max-height: 100%;
    margin:20px 0;
    
}
.personalBio{
    font-size: 14px;
    color:#565673;
    font-weight:300;
    
}
.recentPosts{
    padding: 20px 0;
}
.recentPost{
    border-bottom: 1px solid #eee;
    margin-bottom: 10px;
}

/* .recentPost:not(:last-child) {
    border-bottom: 1px solid #eee;
} */

.recentPost h3{
    font-weight: 500;
    color: #333;
    letter-spacing: 1px;
    font-size:14px;
    color:#6c6c6c;
    margin:0;

}

.recentPost span{
    font-size: 10px;
    font-weight:400;
    color:#6c6c66;
}